<template>
  <div class="main">
    <div v-for="(item, index) in LoanContractList" :key="index" class="card">
      <div class="cardMain" @click="openDialog(item.contractId)">
        <div class="header">
          <div class="headerTitle">
            <span class="header_syd">生源地</span>
            {{item.loanContractId}}
          </div>
          <div class="headerMain">{{item.loanSchoolYear}}</div>
        </div>
        <div class="content">
          <div class="maney">
            <div class="maneyTitle">活动金额</div>
            <div class="maneyNumber">￥{{item.loanMoney}}</div>
          </div>
          <div class="maney">
            <div class="maneyTitle">活动余额</div>
            <div class="maneyNumber">￥{{item.loanBalance}}</div>
          </div>
        </div>
      </div>
      <div class="cardBottom">
        <el-button type="text" icon="el-icon-s-order" @click="openPay(index)">提前还款</el-button>
        <el-button type="text" icon="el-icon-download" @click="exportContract">导出电子合同</el-button>
      </div>
    </div>
    <el-dialog title="合同信息" :visible.sync="dialogVisible" width="900px">
      <div class="dialogMain">
        <div class="title">
          合同编号：{{dialogInfo.contractId}}
          <span>-生源地</span>
        </div>
        <el-row :gutter="20">
          <el-col :span="12">
            <div>
              贷款金额：
              <span>{{dialogInfo.loanMoney}}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div>
              发放日期：
              <span>{{dialogInfo.loanDate.toString().substr(0,10)}}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div>
              合同金额：
              <span>{{dialogInfo.loanBalance}}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div>
              到期日期：
              <span>{{dialogInfo.endDate.toString().substr(0,10)}}</span>
            </div>
          </el-col>
        </el-row>
        <div class="text">
          贴息截止日：
          <span>{{dialogInfo.userGraduationTime.toString().substr(0,10)}}</span>
        </div>
        <div class="text">
          贷款办理机构：
          <span>{{dialogInfo.fundingCenterName}}</span>
        </div>
        <el-button style="width: 200px" type="danger">提前还款</el-button>

        <el-tabs value="jinqi">
          <el-tab-pane label="近期应还" name="jinqi">
            <div>{{dialogInfo.currentDateRepay}}</div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
    <el-dialog
      title="提前还款"
      :visible.sync="dialogVisible2"
      :close-on-click-modal="false"
      width="700px"
    >
      <div class="payTitle">{{dialogInfo2.loanSchoolYear}}</div>
      <el-row :gutter="40">
        <el-col :span="12">
          <div :class="!dialogInfo2.repaymentType?'focusBox':'payBox'" @click="dialogInfo2.repaymentType = 0">
            <div class="text">全额还款（本金）</div>
            <div class="payment">{{dialogInfo2.loanBalance}}</div>
            <i class="el-icon-circle-check"></i>
          </div>
        </el-col>
        <el-col :span="12">
          <div :class="dialogInfo2.repaymentType?'focusBox':'payBox'" @click="dialogInfo2.repaymentType = 1">
            <div class="text">部分还款（本金）</div>
            <div style="margin-top:10px">
              <el-input
                size="small"
                v-model="dialogInfo2.repaymentAmount"
                type="number"
                min="500"
                placeholder="还款金额最低500,且100的倍数"
              ></el-input>
            </div>
            <i class="el-icon-circle-check" ></i>
          </div>
        </el-col>
      </el-row>
      <div class="footBtn">
        <el-button @click="dialogVisible2 = false">取消</el-button>
        <el-button type="primary" @click="submitPay">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "second",
      dialogInfo: {
        loanDate: "",
        loanMoney: "",
        loanBalance: "",
        endDate: "",
        contractId: "",
        fundingCenterName: "",
        userGraduationTime: "",
        currentDateRepay: ""
      },
      dialogInfo2: {
        loanSchoolYear: "2019-2020合同",
        contractId: "",
        loanBalance: "3000",
        repaymentAmount: "",
        repaymentType: 0
      },
      LoanContractList: [],
      dialogVisible: false,
      dialogVisible2: false,
    };
  },
  created() {
    this.$axios
      .post("/user/getAllLoanContract", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          this.LoanContractList = data;
        } else if (status == 401) {
          this.$message({
            message: "获取失败",
            type: "error"
          });
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    closeDialog() {
      this.dialogVisible = false;
    },
    openDialog(contractId) {
      this.$axios
        .post("/loan/loanContractInfo", {
          userLoginAccount: sessionStorage.getItem("userLoginAccount"),
          contractId
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.dialogInfo = data;
            this.dialogVisible = true;
          } else if (status == 401) {
            this.$message({
              message: "获取失败",
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    exportContract() {
      this.$axios
        .post("/loan/exportAppForm", {
          userLoginAccount: sessionStorage.getItem("userLoginAccount")
        })
        .then(({ status, data }) => {
          if (status == 200) {
            console.log(data);
          } else if (status == 401) {
            this.$message({
              message: "获取失败",
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    openPay(index) {
      this.dialogInfo2 = this.LoanContractList[index];
      this.dialogVisible2 = true;
    },
    submitPay(){
      const {contractId,repaymentType,loanBalance,repaymentAmount} = this.dialogInfo2;
      this.$axios
        .post("/user/pay", {
          userLoginAccount: sessionStorage.getItem("userLoginAccount"),
          contractId:contractId,
          repaymentAmount:!repaymentType?parseFloat(loanBalance):parseFloat(repaymentAmount)
        })
        .then(({ status }) => {
          if (status == 200) {
            this.$message({
              message: "支付成功",
              type: "success"
            });
            this.dialogVisible2 = false;
          } else if (status == 401) {
            this.$message({
              message: "支付失败",
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.main {
  padding: 0 40px;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  .card {
    width: 390px;
    background: #fff;
    text-decoration: none;
    outline: none;
    height: 300px;
    border-radius: 4px;
    border-top: 4px solid #fd4e49;
    display: flex;
    flex-direction: column;
    margin: 0 23px 37px;
    &:hover {
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      .cardMain {
        background: rgba(220, 237, 250, 0.699);
      }
    }
    .cardMain {
      display: block;
      padding: 21px 23px 25px;
      flex-grow: 1;
      .header {
        margin-bottom: 20px;
        .headerTitle {
          color: #7f8f93;
          font-weight: 400;
          font-size: 14px;

          .header_syd {
            color: #009edf;
            margin-left: 5px;
            font-weight: bold;
          }
        }
        .headerMain {
          font-size: 23px;
          color: #000;
          font-weight: 500;
        }
      }
      .content {
        display: flex;
        text-align: left;
        .maney {
          width: 50%;

          .maneyTitle {
            color: #7c7c7c;
            font-weight: 400;
            font-size: 14px;
          }
          .maneyNumber {
            font-size: 25px;
            color: #fc4e49;
            font-weight: bold;
          }
        }
      }
    }
    .cardBottom {
      width: 100%;
      display: flex;
      .el-button {
        padding: 17px 12px 20px;
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        /deep/ i {
          font-size: 1.3rem;
          padding: 7px;
          border-radius: 100%;
        }
        /deep/ .el-icon-s-order {
          background-color: #ffe9e9;
          color: #f84c48;
        }
        /deep/ .el-icon-download {
          background-color: #d9eeee;
          color: #43afa9;
        }
      }
    }
  }
  /deep/ .el-dialog {
    .el-dialog__header {
      .el-dialog__title {
        font-size: 24px;
        font-weight: 500;
      }
    }
    .el-dialog__body {
      padding: 20px;
      .dialogMain {
        .title {
          margin-bottom: 13px;
          font-size: 16px;
          font-weight: bold;
          span {
            color: rgb(0, 154, 182);
          }
        }
        .el-row {
          .el-col {
            margin-bottom: 10px;
            font-weight: bold;
            span {
              font-weight: 300;
            }
          }
        }
        .text {
          margin-bottom: 10px;
          font-weight: bold;
          span {
            font-weight: 300;
          }
        }
      }
      .payTitle {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      .payBox {
        padding: 20px 15px;
        border: 2px solid #69b7dc;
        height: 70px;
        display: flex;
        flex-direction: column;
        i{
          display: none;
        }
        &:hover{
          cursor: pointer;
        }
      }
      .text {
        font-size: 17px;
        margin-bottom: 5px;
      }
      .payment {
        font-size: 16px;
      }
      .focusBox {
        padding: 20px 15px;
        border: 2px solid #92ce97;
        height: 70px;
        display: flex;
        flex-direction: column;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        i {
          position: absolute;
          right: 10px;
          top:10px;
          font-size: 40px;
          color: #92ce97;
        }
      }
      .footBtn {
        margin-top: 20px;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}
</style>